@import "../mixins.scss";

.search-field-container {
  position: relative;
  --border-width: 2px;

  .icon {
    position: absolute;
    height: 2rem;
    left: .5rem;
    // Center vertically
    top: calc(.25rem - calc(var(--border-width) / 2));
  }

  input {
    background: none;
    --icon-size: 2rem;
    height: 2.5rem;
    width: 100%;
    border-radius: 1.25rem;
    border: 2px solid var(--canary);
    // Required for rounded gradient border
    border-color: transparent;
    background-image: linear-gradient(var(--azure-glass), var(--azure-glass)), var(--track);
    background-origin: border-box;
    background-clip: padding-box, border-box;

    padding: .25rem;
    padding-left: calc(.5rem + var(--icon-size) + .25rem);
    color: var(--turbo-blue);
    @include body;
    
    &::placeholder {
      @include body;
      font-style: italic;
      color: rgba(var(--canary--rgb), 70%);
      opacity: .5;
    }
  }
}